  .order-right{
    float: right;
  }
  .order-img{
    width: 5rem;
    height: 5rem;
    background: bisque;
    margin-right: 1rem;
  }
  .order-right-title{
    font-weight: bold;
  }
  .order-content-text{
    margin: 0  0.5rem;
    padding: 0.5rem 0;
    border-bottom:1px solid #F1F1F1;
    display: flex;
  }
</style>



